import React, { useState, useContext } from 'react';
import { useSelector, useDispatch } from 'react-redux'
import { makeStyles } from '@material-ui/core/styles';
import { socket } from '../components/WebSocket'
import Card from '../components/Card'
import { accept } from '../reducer'
import Modal from '@material-ui/core/Modal'
import { mobileContext } from '../containers/App'





const Result = () => {

  const isMobile = useContext(mobileContext)

  const useStyles = makeStyles((theme) => ({
    paper: {
      position: 'absolute',
      width: isMobile ? '80%' : '40%',
      backgroundColor: theme.palette.background.paper,
      border: '2px solid #000',
      boxShadow: theme.shadows[5],
      padding: theme.spacing(2, 4, 3),
      overflow: 'scroll'
    },
  }));
  
  const getModalStyle = {
    top: isMobile ? '5%' : '10%',
    bottom: isMobile ? '5%' : '',
    left: isMobile ? '10%' : '30%',
    right: isMobile ? '10%' : '30%'
  }

  const { 
    point, 
    yakuNameArr, 
    uradora, 
    gameEnd, 
    yakuman, 
    pan, 
    win, 
    resultCards, 
    myScore, 
    draw
   } = useSelector(state => state.gameState)
  const dispatch = useDispatch()
  
  const classes = useStyles();
  const [modalStyle] = useState(getModalStyle);


  const handleClose = () => {
    socket.emit('accept')
    dispatch(accept())
    // gameEnd false
    //   
    // emit?
  };

  const UradoraImg = (uradora) => {
    return (
      <div className='tc dib br3 ma1 bw2 shadow-5'>
        <img src={uradora.img} width='39.6px' height='70.8px' alt='card' className='br3 cards'/> 
      </div>
    )
  }

  const yakumanText = {
    1: '',
    2: '',
    3: '3',
    4: '4'
  }

  const body = (
    <div style={modalStyle} className={classes.paper}>
      {point === -8000
      ? <h1></h1>
      : win
        ? <h1> </h1>
        : <h1></h1>
      }
      <div className='Result-container'>
        {resultCards.map((card, i) => {
          return(
            <Card card={card} width={39.6} height={70.8} key={i}></Card>
          
          // <div className='tc dib br3 ma1 bw2 shadow-5'>
          //   <img src={e.img} width='39.6px' height='70.8px' alt='card' className='br3 cards'/> 
          //   {/* 33:59 */}
          // </div>
          )}
        )}
      </div>
      
      {UradoraImg(uradora)}
      <hr />
      <div>
        {yakuNameArr.map(yakuName => {
          return (<h4>{yakuName}</h4>)
        })}
      <hr />
      {
        yakuman >= 1
        ? <h1>{yakumanText[yakuman]}{' '} {32000*yakuman}</h1>
        : <h3>{pan} {point}</h3>
      }
        
      </div>
      {
        win
        ? <h4>{myScore - point}{' '}{' '}{myScore} </h4>
        : <h4>{myScore + point}{' '}{' '}{myScore} </h4>
      }
      <span style={{color:'red'}}>   </span>

    {/*  :   or  or */}
    {/* , ,  */}
    </div>
  )

  const drawBody = (
    <div style={modalStyle} className={classes.paper}>
      <h1></h1>
    </div>
  )

  return (
    <div>
      <Modal
        open={gameEnd} // gameEnd?
        onClose={handleClose}
        aria-labelledby="simple-modal-title"
        aria-describedby="simple-modal-description"
      >
        {draw ? drawBody : body}
      </Modal>
    </div>
  );
};

export default Result;