import React, { useState } from 'react';
import {  useSelector } from 'react-redux'
import { socket } from './WebSocket'

const Lobby = () => {

  const { isTwoUser, myName, opponentName, roomID } = useSelector(state => state.gameState)
  
  const [joinID, setJoinID] = useState('')

  return (
    <div>
      {
        isTwoUser 
        ?
        <div>
          <h4 className='blue'> !</h4>
          <h1 style={{color:'yellow'}}>{myName}{' '} VS {' '}{opponentName}</h1>
        </div>
        : 
        <div>
          <input type='text' placeholder='[  ]' 
          onChange={(e) => setJoinID(e.target.value)}>
          </input>
          <button onClick={() => socket.emit('joinroom', { joinID: joinID, name: myName })}> </button>
          <h4 className='blue'>{roomID}     ...</h4>
          <h4 className='yellow'>      </h4>
        </div>
      }
    </div>
  );
};

export default Lobby;